import React, { Component } from 'react';
import styled from 'styled-components';
import FloatingLabel from 'floating-label-react'

const ContactWrapper = styled.article`
  width: 25vw;
  min-width: 360px;
  height: auto;
  position: absolute;
  right: 72px;
  top: 72px;
  background: #fff;
  box-shadow: 0 2px 16px -2px rgba(159,159,159,0.32);
`;

const ContactHeader = styled.h3`
  width: 100%;
  padding: 24px 0;
  background: #0f0f0f;
  color: #fff;
  font-size: 1.5rem;
  font-weight: 500;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const ContactForm = styled.form`
  width: 100%;
  padding: 24px;
  box-sizing: border-box;
  display: flex;
  flex-flow: column nowrap;
`;

const ContactFormRow = styled.div`
  width: 100%;
  display: flex;
  flex-flow: row nowrap;

  & label input {
    width: calc(50% - 12px);
  }
`;

const ContactFormFieldset = styled.fieldset`
  border: 0;
`;

class Contact extends Component {

  render() {
    return (
      <ContactWrapper className="contact">
        <ContactHeader className="contact__header">
          Let us buy you a beer
        </ContactHeader>
        <ContactForm className="contact__form" netlify method="POST">
            <ContactFormRow className="contact__form--row">
              <FloatingLabel
                type="text"
                name="fname"
                placeholder="First name"
              />
              <FloatingLabel
                type="text"
                name="lname"
                placeholder="Last name"
              />
            </ContactFormRow>
            <input type="email" name="email"/>
          <input type="submit" value="Submit" />
        </ContactForm>
      </ContactWrapper>
    );
  }

}

export default Contact;



